import * as React from "react";
import type { SVGIconProps } from "../../svgIconProps";
import { IconSize } from "../../iconTypes";
import { SVGIconContainer } from "../../svgIconContainer";

export const DirectionRight: React.FC<SVGIconProps> = React.forwardRef<any, SVGIconProps>((props, ref) => {
    const isLarge = props.size! >= IconSize.LARGE;
    const pixelGridSize = isLarge ? IconSize.LARGE : IconSize.STANDARD;
    const translation = `${-1 * pixelGridSize / 0.05 / 2}`;
    return (
        <SVGIconContainer iconName="direction-right" ref={ref}  {...props}>
            <path
                d={isLarge ? "M400 199.6L0 339.6L100 199.6L0 59.6z" : "M320 160.2L0 280.2L80 160.2L0 40.2z"}
                fillRule="evenodd"
                transform-origin="center"
                transform={`scale(0.05, -0.05) translate(${translation}, ${translation})`}
            />
        </SVGIconContainer>
   )
});
DirectionRight.defaultProps = {
    size: IconSize.STANDARD,
};
DirectionRight.displayName = `Blueprint5.Icon.DirectionRight`;
export default DirectionRight;
